import React from "react";
import Layout from "../components/layout";
import { Country } from "country-state-city";
import { initializeApp } from "firebase/app";
import { collection, addDoc, getFirestore } from "firebase/firestore/lite";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { useStaticQuery, graphql } from "gatsby";
import { utils } from "../core/helper";
import * as Yup from "yup";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const Blog = (props) => {
  const { pageContext } = props;
  console.log("pageContext::", pageContext);
  console.log("props::", props);

  const data = useStaticQuery(graphql`
    query MyQuery {
      site {
        id
        siteMetadata {
          firestore {
            apiKey
            appId
            authDomain
            messagingSenderId
            projectId
            storageBucket
          }
        }
      }
    }
  `);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const countries = Country.getAllCountries();

  const app = initializeApp({ ...data.site.siteMetadata.firestore });
  const db = getFirestore(app);

  const handleOnSubmit = async (values) => {
    const clients = collection(db, "clients");
    await addDoc(clients, values);
    utils.showSuccssMsg("We will contact/talk to you soon");
    reset();
  };

  return (
    <Layout>
      <section className="mt-5">
        <div className="container">
          <div className="row ">
            <div className="col-md-12 col-lg-9">
              <div className="row"> 
                  <div className="blog_wrapper mb-3">
                    <h2 className="fs-1 mb-3">{pageContext?.author_name}</h2>
                    <i className="fs-4 text-secondary">Posted On: ({pageContext?.blog_created_date})</i>
                  </div>
                  <div className="blog_img shadow">
                    <img src={pageContext?.image} alt="blog-img" />
                  </div>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: pageContext?.description,
                    }}
                    className="blog-content mt-3"
                  ></div>
                </div>
                {/* <div className="col-md-12 col-lg-5">
                  <div className="blog_img shadow">
                    <img src={pageContext?.image} alt="blog-img" />
                  </div>
                </div> */}
            </div>
            <div className="col-md-12 col-lg-3">
              <div className="card sticky-top section-cta">
                <div className="card-header">Categories</div>
                <div className="card-body ">
                  <form
                    onSubmit={handleSubmit(handleOnSubmit)}
                    className="cta-form"
                  >
                    <div>
                      <label for="full-name">Name</label>
                      <input
                        id="full-name"
                        type="text"
                        placeholder="Enter Your Name"
                        {...register("name")}
                        maxLength={30}
                      />
                    </div>
                    <div>
                      <label for="email">Email address</label>
                      <input
                        id="email"
                        type="email"
                        placeholder="me@example.com"
                        {...register("email")}
                        maxLength={50}
                      />
                    </div>
                    <div>
                      <label for="select-where">
                        Where did you hear from us?
                      </label>
                      <select name="country" {...register("country")}>
                        <option value="">Please choose one option:</option>
                        {countries.map((ele, ind) => {
                          return (
                            <option key={ind} value={ele.isoCode}>
                              {ele.name}
                            </option>
                          );
                        })}
                      </select>
                    </div>
                    <div>
                      <label for="mobile">Skype / Whatsapp / Mobile</label>
                      <input
                        id="mobile"
                        type="tel"
                        placeholder="Skype / Whatsapp / Mobile"
                        {...register("mobile")}
                        maxLength={50}
                      />
                    </div>
                    <div>
                      <label for="content">Content</label>
                      <textarea
                        id="content"
                        placeholder="Type content here"
                        rows={5}
                        className="input"
                        {...register("content")}
                        maxLength={200}
                      ></textarea>
                    </div>
                    <button className="btn btn--form">Submit</button>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <div></div>
        </div>
      </section>
    </Layout>
  );
};

export default Blog;
